.homeOpen__sectionLeft{
    width: 100%;
    max-height: 100vh;
    position: absolute;
    top:0;
    left: 0;
    display: flex;
    @media screen and (max-width:600px) {
        flex-direction: column;
    }
}
.homeOpen__sectionRight{
    width: 100%;
    max-height: 100vh;
    position: absolute;
    top:0;
    display: flex;
    flex-direction: row-reverse;
    @media screen and (max-width:600px) {
        flex-direction: column;
    }
}
.screenComplete{
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(to bottom, #ff0b61,#00073b);
    box-shadow: 0 0 10px #010101;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @media screen and (max-width:600px) {
        max-height: 50vh;
        margin-bottom: 5px;
    }
    .image {
        width: 100px;
        margin-bottom: 50px;
        margin-top: -20px;
        animation: logo_animation 10s infinite;
        @media screen and (max-width:600px) {
            width: 70px;
            margin-top: 5px;
            margin-bottom: 10px;
        }
    }
    @keyframes logo_animation {
        0%{transform: rotate(360deg);}
        100%{transform: rotate(0);}
    }

    h1,p{
        color: white;
        text-transform: uppercase;
        text-align: center;
        @media screen and (max-width:600px) {
            font-size: 18px;
        }
    }
    p{
        margin-top: 15px;
        @media screen and (max-width:600px) {
            font-size: 15px;
        }
    }
    button {
        width: 300px;
        height: 50px;
        border-radius: 30px;
        border: none;
        background-color: #ffffffa2;
        box-shadow: 0 0 1em #444444;
        cursor: pointer;
        text-transform: uppercase;
        font-weight: 800;
        font-size: 15px;
        margin: 80px 0;
        color: #00073b;
        &:hover{
            background-color: #ffffffea;
            color: #ff0b61;
        }
        @media screen and (max-width:600px) {
            margin: 20px 0 10px 0;
        }
    }

}
.screenInput{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    .buttonGoogle {
        border-radius: 30px !important;
        margin-bottom: 30px !important;
        width: 300px;
        justify-content: center;
        > div{
            border-radius: 30px !important;
        }
        >span{
            font-family: 'Raleway', sans-serif !important;
            color: black !important;
            text-transform: uppercase;
        }
    }
    .buttonGit{
        border-radius: 30px !important;
        margin-bottom: 30px !important;
        width: 300px;
        justify-content: center;
        background-color: rgb(255, 255, 255);
        display: inline-flex;
        align-items: center;
        color: rgba(0, 0, 0, 0.54);
        box-shadow: rgb(0 0 0 / 24%) 0px 2px 2px 0px, rgb(0 0 0 / 24%) 0px 0px 1px 0px;
        border: none;
        height: 43px;
        font-family: 'Raleway', sans-serif !important;
        color: black !important;
        text-transform: uppercase;
        cursor: pointer;
        &::before {
            content: '';
            background-image: url('https://github.com/fluidicon.png');
            width: 10px;
            height: 10px;
            padding: 8px;
            margin-right: 29px;
            background-size: cover;
        }
    }
    form.MuiBox-root.css-xn6ruc {
        display: flex;
        flex-direction: column;
    }
    &__noRegister{
        cursor: pointer;
        margin-top: 0;
        width: 210px;
        text-align: center;
        &:hover{
            color: #ff0b61;
            transform: scale(1.2);
        }
    }
    &__submit{
        width: 300px;
        height: 50px;
        border-radius: 30px;
        border: none;
        background-color: #ff0b61;
        box-shadow: 0 0 1em #cecdcd;
        cursor: pointer;
        text-transform: uppercase;
        font-weight: 800;
        font-size: 15px;
        margin: 20px 0;
        color: white;
        &:hover{
            background-color: #00073b;
            color: white;
        }
    }
    .arrowBack {
        fill: #00073b !important;
        margin-right: 284px;
        height: 50px;
        cursor: pointer;
        width: 20px;
        &:hover{
            fill: #ff0b61 !important;
        }
    }
    .register__geral {
        display: flex;
        flex-direction: column;
        padding: 10px 0;
        width: 250px;
        margin-left: 25px;
        .localLabel {
            color: #00073b;
            text-transform: uppercase;
            font-size: 12px;
        }
        input {
            outline: 0;
            border-width: 0 0 2px;
            border-color: #00073b;
            width: 100%;
            height: 30px;
            &:focus {         
                border-color: #ff0b61;
            }
        }
    }
}
.modalAviso{
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    background-color: #00073b70;
    display: flex;
    align-items: center;
    justify-content: center;
    > div{
        width: 370px;
        height: 300px;
        background-color: white;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 0 15px;
        .close{
            position: relative;
            top: calc(-1*(50% - 15px)/2);
            left: calc((100% - 15px)/2);
            >button{
                width: 20px;
                height: 20px;
                border: transparent;
                background-color: transparent;
                font-size: 20px;
                color: #ff0b61;
                cursor: pointer;
            }
        }
        .icon{
            font-size: 30px;
            padding: 30px 0;
            margin-top: -60px;
            color: #ff0b61;
        }
        >h1{
            font-size: 15px;
            text-align: center;
            line-height: 20px;
            color: #00073b;
        }
    }
}