.section__menuLateral{
    width: 15%;
    min-width: 200px;
    height: auto;
    min-height: 100vh;
    padding: 30px 10px;
    background-color: #ff0b61;
    color: #00073b;
    z-index: 9;
    @media screen and (max-width:600px) {
        min-height: 100px;
        width: 100%;
    }
    .profile{
        display: flex;
        flex-direction: column;
        align-items: center;
        .imageUser{
            width: 60px;
            height: 60px;
            border-radius: 50%;
            box-shadow: 0 0 0.5em #414040;
        }
        .iconUserContainer{
            width: 60px;
            height: 60px;
            border-radius: 50%;
            box-shadow: 0 0 0.5em #414040;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #00073b;
        }
        .iconUser{
            font-size: 40px;
            color: #ff0b61;
        }
        >p{
            margin-top: 10px;
            font-size: 15px;
            text-align: center;
            text-transform: uppercase;
        }
        button{
            width: 100px;
            height: 30px;
            background-color: white;
            border-radius: 15px;
            border: none;
            cursor: pointer;
            margin-top: 10px;
            color: #00073b;
            text-transform: uppercase;
            font-size: 10px;
           
        }
    }
}