*{
    margin: 0;
    padding: 0;
    font-family: 'Raleway', sans-serif;
}
.section__App{
    display: flex;
    align-items: center;
    @media screen and (max-width:600px) {
        flex-direction: column;
    }
}
.section__geral {
    background-color: white;
    width           : 100%;
    min-height      : 100vh;
    padding         : 30px;
    @media screen and (max-width:600px) {
        width: calc(100% - 60px);
    }
    .image__geral{
        position: absolute;
        left: 42%;
        height: 200px;
        width: 50px;
        transform: rotate(270deg);
        top: 0;
        object-fit: cover;
        @media screen and (max-width:600px) {
            position: absolute;
            left: 0;
            top: 200px;
            animation: none;
            right: 0;
            transform: rotate(90deg);
        }
        animation: animationKF 2s infinite ;
        @keyframes animationKF {
            0%{
                left: 42%;
            }
            100%{
                left: 15%;
            }
        }
    }
}